<template>
  <div class="box">
    <input type="file" ref="fileInput" @change="handleFileChange" /> <img :src="imageUrl" alt="" v-if="imageUrl" />
    <el-input v-model="input" placeholder="请输入用户名"></el-input>
    <el-input v-model="inpu" placeholder="请输入登录用户"></el-input>
    <el-radio v-model="radio" label="1">男</el-radio>
    <el-radio v-model="radio" label="0">女</el-radio>
    <el-input v-model="inp" placeholder="请输入手机号"></el-input>
    <el-button type="primary" @click="add()">确认添加</el-button>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  data() {
    return { imageUrl: '', input: '', inpu: '', inp: '', radio: '1' }
  },
  methods: {
    add() {
      console.log(1111)
      let obj = { name: this.input, userid: this.inpu, sex: 1, iphone: this.inp }
      axios
        .post('http://47.94.4.201/index.php/index/index/mycenter', obj)
        .then(res => {
          if (res.data.msg == '用户名重复') {
            this.$notify({
              title: '名字重复',
              message: '',
              type: 'warning'
            })
            return
          }
          this.$notify({
            title: '成功',
            message: '',
            type: 'success'
          }) // console.log(res.data.msg)
        })
        .catch(error => {
          console.error(error) // 根据需要处理错误
        })
    },
    handleFileChange(event) {
      const file = event.target.files[0]
      const reader = new FileReader()
      reader.onload = e => {
        const base64Data = e.target.result
        const formData = new FormData()
        formData.append('imgurl', base64Data)
        axios
          .post('https://www.zzgoodqc.cn/index.php/index/upload/uploadimg', formData)
          .then(res => {
            const imagePath = res.data.data.url // 拼接接口域名或IP
            this.imageUrl = `http://47.94.4.201/${imagePath}`
            console.log(res.data)
          })
          .catch(error => {
            console.error(error) // 根据需要处理错误
          })
      }
      reader.readAsDataURL(file)
    },
    computed: {
      fullImageUrl() {
        return this.imageUrl
      }
    }
  },
  created() {}
}
</script>
<style>
.box {
  width: 300px;
  margin: auto;
}
</style>
