import { apiPost } from './api'

export function getList(data) {
  return new Promise((resolve, reject) => {
    apiPost('https://www.zzgoodqc.cn/index.php/index/callcenter/getheaders', data)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}
